@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@600&display=swap);
* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
}

/* Navbar */
.navbar h1 {
  color: #006bcf;
  font-size: 44px;
}

.navbar {
  background-color: #b9e5d2;
  padding: 18px;
  display: flex;
  align-items: center;
  max-width: 1500px;
  margin: 0 auto;
  border-bottom: 15px solid #f2f2f2;
}

.navbar a {
  margin-left: 18px;
  text-decoration: none;
  padding: 8px;
}

.navbar a:hover {
  /* color: #f1356d; */
  color: #0693e3;
}

.navbar .nav-hover-item:hover {
  color: white;
  background-color: #0693e3;
  border-radius: 10px;
}

.navbar .nav-menue {
  padding-left: 30%;
}

#root {
  height: 100%;
}

/* Home page*/
.home-page {
  margin: auto;
  padding: 4%;
}

h1 {
  font-family: sans-serif;
  font-size: 40px;
  padding-bottom: 25px;
}

.home-page article {
  font-size: 20px;
  padding-bottom: 40px;
}

.home-page ol {
  padding-top: 20px;
}
.home-page li {
  font-size: 22px;
}
.home-page p {
  padding: 10px;
}
.home-page h2 {
  font-family: sans-serif;
}
.home-page h3 {
  font-family: sans-serif;
}

/* sqldb form*/
.sqldb input,
.sqldb textarea {
  align-items: center;
  margin: auto;
  width: 30%;
  font-size: 34px;
  padding: 20px;
  border: 4px solid #ddd;
  box-sizing: border-box;
  display: block;
}

.sqldb {
  font-size: larger;
  text-align-last: center;
}

.sqldb button {
  background-color: #b9e5d2;
  color: #333;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  font-size: 14px;
  cursor: pointer;
}

.sqldb .form .plate p {
  background-color: #f2f2f2;
}

.plate {
  border-radius: 5px;
  margin: 18px auto;
  width: 500px;
  height: 45px;
  border: 2px solid black;
}
.sqldb .submit-button {
  background-color: bisque;
  color: #333;
  border: 0;
  padding: 16px;
  border-radius: 16px;
  font-size: 20px;
  cursor: pointer;
}

/* Camera*/
.camera-shot {
  padding: 4%;
}
.camera-header {
  margin: auto;
  align-items: center;
}

/* Distance Sensor */
.dist-sense h1 {
  padding: 4%;
}

.dist-sense h2 {
  font-family: sans-serif;
}

.dist-sense ol {
  padding-bottom: 20px;
}

